// Small tablets and large smartphones (landscape view)
$screen-sm-min: 576px;

// Small tablets (portrait view)
$screen-md-min: 768px;

// Tablets and small desktops
$screen-lg-min: 992px;

// Large tablets and desktops
$screen-xl-min: 1200px;

// Small devices
@mixin sm {
  @media (min-width: #{$screen-sm-min}) {
    @content;
  }
}

// Medium devices
@mixin md {
  @media (min-width: #{$screen-md-min}) {
    @content;
  }
}

// Large devices
@mixin lg {
  @media (min-width: #{$screen-lg-min}) {
    @content;
  }
}

// Extra large devices
@mixin xl {
  @media (min-width: #{$screen-xl-min}) {
    @content;
  }
}

$dark: #0b0c10;
$dark-grey: #1f2833;
$grey: #666666;
$light-grey: #c5c6c7;
$highlight: #66fcf1;
$highlight-dark: #45a29e;

html,
body {
  color: $grey !important;
}

.sub-container {
  section {
    min-height: 100%;
    @include sm {
      min-height: 100vh;
    }
  }
}

.svg-triangle {
  height: 100px;
  width: 100%;
  display: none;
  @include sm {
    display: block;
  }
  svg {
    width: 100%;
    height: 100px;
    polygon {
      fill: $highlight-dark;
    }
  }
}
.center-items {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
h1,
h2,
h3 {
  color: $highlight-dark;
  font-weight: bold;
}
h1 {
  @include sm {
    font-size: 3rem;
  }
}

