.experience {
  display: block;
  padding-bottom: 5rem;
  .vertical-timeline {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 2rem;
    &::before {
      background: $light-grey;
      width: 2px;
    }
    .vertical-timeline-element-icon {
      background: $dark-grey;
      color: #fff;
      font-size: 1.33rem;
      box-shadow: none;
    }
    .vertical-timeline-element-content {
      background: transparent;
      color: $dark-grey;
      box-shadow: none;
      padding-top: 0;
      .vertical-timeline-element-content-arrow {
        border-right: 7px solid transparent;
      }
      .vertical-timeline-element-date {
        color: $grey;
      }
      .date-details {
        font-size: 0.8rem;
        color: $grey;
      }
      .location-details {
        font-size: 0.8rem;
        color: $grey;
      }
    }
  }
}
