.project-card {
  position: relative;
  display: inline-block;
  color: #ffffff;
  width: 100%;
  .text-container {
    position: relative;
    display: inline-block;
    margin-bottom: 10rem;
    background: $dark-grey;
    width: 100%;
    float: left;
    margin-right: 0;
    @include sm {
      width: 60%;
      margin-right: -2%;
      float: left;

      &.left-card {
        margin-right: 0;
        float: right;
      }
    }

    h2 {
      color: #ffffff;
      margin-bottom: 1rem;
    }

    .tech-row {
      font-size: 2rem;
      .icon-container {
        padding-right: 1rem;
      }
      .image-icon {
        height: 32px;
        width: auto;
      }
    }
  }

  .image-container {
    position: relative;
    display: inline-block;

    margin-right: 0;
    width: 100%;
    height: auto;
    @include sm {
      width: 42%;
      float: right;

      &.left-card {
        margin-right: -2%;
        float: left;
      }
    }
  }
  &.mobile-view {
    display: block;
    @include sm {
      display: none;
    }
  }
  display: none;
  @include sm {
    display: block;
  }
}

/*! Flickity v2.1.2
https://flickity.metafizzy.co
---------------------------------------------- */

.flickity-enabled {
  position: relative;
}

.flickity-enabled:focus {
  outline: none;
}

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

/* draggable */

.flickity-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
  tap-highlight-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

/* ---- flickity-button ---- */

.flickity-button {
  position: absolute;
  background: transparent;
  border: none;
  color: #333;
}

.flickity-button:hover {
  color: #111;
  cursor: pointer;
}

.flickity-button:focus {
  outline: none;
  box-shadow: 0 0 0 5px #19f;
}

.flickity-button:active {
  opacity: 0.6;
}

.flickity-button:disabled {
  opacity: 0.3;
  cursor: auto;
  /* prevent disabled button from capturing pointer up event. #716 */
  pointer-events: none;
}

.flickity-button-icon {
  fill: #333;
}

/* ---- previous/next buttons ---- */

.flickity-prev-next-button {
  top: 55%;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  /* vertically center */
  transform: translateY(-50%);
}

.flickity-prev-next-button.previous {
  left: 1rem;
}
.flickity-prev-next-button.next {
  right: 1rem;
}
/* right to left */
.flickity-rtl .flickity-prev-next-button.previous {
  left: auto;
  right: 10px;
}
.flickity-rtl .flickity-prev-next-button.next {
  right: auto;
  left: 10px;
}

.flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}

/* ---- page dots ---- */

.flickity-page-dots {
  position: absolute;
  width: 100%;
  bottom: -25px;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}

.flickity-rtl .flickity-page-dots {
  direction: rtl;
}

.flickity-page-dots .dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 8px;
  background: #333;
  border-radius: 50%;
  opacity: 0.25;
  cursor: pointer;
}

.flickity-page-dots .dot.is-selected {
  opacity: 1;
}
