.about {
  h1 {
    margin-top: 4rem;
    @include sm {
      margin-top: 0;
    }
  }
  .description-row {
    @include sm {
      margin-top: 4rem;
    }
    .description-col {
      padding-top: 2rem;
      @include sm {
        padding-top: 0;
      }
      .description {
        display: block;
        text-align: justify;
        .external-profiles {
          @include sm {
            margin-top: 4rem;
          }
          .fab {
            color: $grey;
            &:hover {
              color: $highlight-dark;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  .my-avatar {
    width: 10rem;
    @include sm {
      width: 15rem;
    }
  }
}
