.technology-item {
  background-color: #fff;
  color: $grey;
  border-radius: 10px;
  min-height: 25rem;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  h1,
  h2 {
    color: $highlight-dark;
  }
  h5 {
    color: $dark;
  }
}
